<template>
    <div class="box">
        <Head>
            <img src="../../images/110.png" alt="" slot="left" @click="aaa"/>
        </Head>
        <div class="content">
            <input type="text" placeholder="请输入你的姓名" v-model="name"> 
            <input type="text" placeholder="小区/写字楼/学校" @click="sub" v-model="val"> 
            <input type="text" placeholder="请填写详细送餐地址" v-model="address"> 
            <input type="text" placeholder="请填写能够直接联系到你的手机号" v-model="phone"> 
            <input type="text" placeholder="备用联系电话（选填）" v-model="beiphone"> 
            <button @click="add">提交</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Head from  '../Head/Header.vue'
export default {
    components:{
        Head
    },
    data() {
        return {
          val:'',
          name:'',
          address:'',
          phone:'',
          beiphone:''
        }
  },
   mounted(){
     this.val = this.$route.query.name
    },
  methods: {
    aaa(){
        this.$router.go(-1)
    },
    sub(){
      this.$router.push({name:'Sea'})
    },
    add(){
      this.axios.post("https://elm.cangdu.org/v1/users/"+this.user_id+"/addresses",{
        
      })
    }
  }
}
</script>

<style lang="less" scoped>
.box{
      width: 100%;
      header{
        width: 100%;
        height: 0.8rem;
        background: #3792E5;
        position: relative;
        img{
            position: absolute;
            top: 0.3rem;
            left: 0.2rem;
            width: 0.3rem;
        }
      }
      .content{
        width: 100%;
        position: relative;
        input:nth-of-type(1){
          width: 6.5rem;
          height: 0.5rem;
          position: absolute;
          top: 0.5rem;
          left: 0.3rem;
          text-indent: 0.2rem;
          font-size: 0.3rem;
          font-weight: 700;
        }
        input:nth-of-type(2){
          width: 6.5rem;
          height: 0.5rem;
          position: absolute;
          top: 1.4rem;
          left: 0.3rem;
          text-indent: 0.2rem;
          font-size: 0.3rem;
          font-weight: 700;
        }
        input:nth-of-type(3){
          width: 6.5rem;
          height: 0.5rem;
          position: absolute;
          top: 2.3rem;
          left: 0.3rem;
          text-indent: 0.2rem;
          font-size: 0.3rem;
          font-weight: 700;
        }
        input:nth-of-type(4){
          width: 6.5rem;
          height: 0.5rem;
          position: absolute;
          top: 3.2rem;
          left: 0.3rem;
          text-indent: 0.2rem;
          font-size: 0.3rem;
          font-weight: 700;
        }
        input:nth-of-type(5){
          width: 6.5rem;
          height: 0.5rem;
          position: absolute;
          top: 4.1rem;
          left: 0.3rem;
          text-indent: 0.2rem;
          font-size: 0.3rem;
          font-weight: 700;
        }
        button{
          position: absolute;
          width: 6.55rem;
          left: 0.3rem;
          height: 0.6rem;
          top: 5rem;
          font-size: 0.4rem;
          border: none;
          background: #4CD964;
          color: white;
        }
      }
   }
</style>


